.Friend{
    background-color: rgb(184, 184, 184);
    width: 300px;
    border-radius: 16px;
    cursor: pointer;
    .Friend-Content{
        .Header{
            .Header-Content{
                position: relative;
                .Cover{
                    border-radius: 16px 16px 0 0;
                    background-color: rgb(54, 54, 54);
                    height: 100px;
                    img{
                        border-radius: 16px 16px 0 0;
                        width: 100%;
                        height: 100%;
                    }
                }

                .Profile{
                    position: absolute;
                    top: 80%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    
                    img{
                        border: 4px solid rgb(184, 184, 184);
                        height: 100px;
                        width: 100px;
                    }
                    .Icon-Bg{
                        border: 4px solid rgb(184, 184, 184);
                        background-color: rgb(72, 67, 67);
                        height: 100px;
                        width: 100px;
                        svg{
                            height: 80%;
                            width: 80%;
                        }

                    }
                }

            }
        }
        .Body{
            padding: 50px 0 0 0;
            .Body-Content{

            }
        }
        .Footer{
            padding: 0 0 30px 0;
            border-radius: 0 0 16px 16px;
            .Footer-Content{
                button{
                    width: 180px;
                }
            }
        }
    }
}